import React from 'react';


class chuanhao extends React.Component{

    constructor(props){
        super(props)
        this.state={
            bg:0
        };
    }

    render() {

        return(

            <div className="weui-search-bar" id="searchBar">
                <form className="weui-search-bar__form">
                    <div className="weui-search-bar__box">
                        <i className="weui-icon-search"></i>
                        <input type="search" className="weui-search-bar__input" id="searchInput" placeholder="输入交易单号后八位或扫码进行"/>
                        <a href="javascript:" className="weui-icon-clear" id="searchClear"></a>
                    </div>
                    <label className="weui-search-bar__label" id="searchText">
                        <i className="weui-icon-search"></i>
                        <span>搜索</span>
                    </label>
                </form>
                <a href="javascript:" className="weui-search-bar__cancel-btn" id="searchCancel">取消</a>
            </div>
        )
    }
}
export default chuanhao;