import React from 'react';

import { List, InputItem, WhiteSpace,Toast } from 'antd-mobile';

import 'antd-mobile/dist/antd-mobile.css';
import '../config';
import $ from "jquery";
const Item = List.Item;
const Brief = Item.Brief;
class xsd extends React.Component {

    constructor(props) {
        super(props)
        this.state = {
            dataList:''
        };
        this.getshuju=this.getshuju.bind(this);
    }
    componentDidMount() {
        this.getshuju()
    }
    componentWillMount() {

    }

    getshuju(){
        var that=this;
        Toast.loading('Loading...', 0, () => {
            console.log('Load complete !!!');
        });
        this.serverRequest = $.ajax('http://nightly-dev.99tone.com/mapi/data/sales/salesDocs', {
            type: 'post',
            headers: {Authorization: 'Bearer ' + global.data.token},
            data: {pindex:1,psize:20},
            success(res) {
                   Toast.hide()
                that.setState({
                    dataList:res.rows
                })
            }
        })
    }

    render() {


        return (
            <div>

                <WhiteSpace/>
                <List renderHeader={() => '销售历史'} className="my-list">
                    {this.state.dataList == '' ? '' :this.state.dataList.map((list)=>
                        <Item
                            arrow="horizontal"
                            thumb="https://zos.alipayobjects.com/rmsportal/dNuvNrtqUztHCwM.png"
                            multipleLine
                            onClick={() => {}}
                            key={list.id}
                        >
                            {list.docMakerRealName} <Brief>{list.docId}  ￥{list.sumMoney}</Brief><Brief>{list.createDate}  {list.store}）</Brief>
                        </Item>
                    )}

                </List>
            </div>
        )
    }
}

export default xsd;