import React from 'react';


class myshoukuan extends React.Component{

    constructor(props){
        super(props)
        this.state={
            bg:0
        };
    }
    click(e){
        this.setState({
            bg:e
        })
    }
    render() {

        return(
            <div className='myshoukuan'>
                <div className={this.state.bg==0?'myshoukuan-flex active':'myshoukuan-flex'}  onClick={this.click.bind(this,0)}>不区分</div>
                <div className={this.state.bg==1?'myshoukuan-flex active':'myshoukuan-flex'}  onClick={this.click.bind(this,1)}>按规格</div>
                <div className={this.state.bg==2?'myshoukuan-flex active':'myshoukuan-flex'}  onClick={this.click.bind(this,2)}>成本价</div>
                <div className={this.state.bg==3?'myshoukuan-flex active':'myshoukuan-flex'}  onClick={this.click.bind(this,3)}>采购价</div>

            </div>
        )
    }
}
export default myshoukuan;